.vidoeCard{
    width: 30vmax;
    height: 48vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0.5vmax;
    
    cursor: pointer;
}
.vidoeCard > a{
    padding: 2.5vmax;
    transition: all 0.5s;
    border: none;
    border-radius: 100%;
    background-color: rgba(255,255,255,0.096);

}
.vidoeCard>a> svg{
    fill:(255,255,255);
}
.vidoeCard:hover a{

    background-color: rgba(255,255,255,0.671);
}
.vidoeCard p{
    color: white;
    margin: 1vmax;

}